.burger-menu {
  position: absolute;
  z-index: 15;
  pointer-events: none;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  box-sizing: border-box;
}
.burger-menu.active {
  pointer-events: all;
}
.burger-menu__background {
  background-color: #fff;
  width: 100vw;
  height: 100vh;
  z-index: 1;
  pointer-events: none;
}
.burger-menu__button {
  width: 40px;
  height: 40px;
  border: none;
  display: flex;
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  position: absolute;
  z-index: 5;
  top: 4.5%;
  right: 6.5%;
  pointer-events: all;
  padding: 0;
}
.burger-menu-nav__support-text {
  font-size: 14px;
  color: #348ff3;
  text-align: right;
  margin-right: 16px;
  justify-self: flex-end;
}
